"use client";

import Link from "next/link";
import { useAuth } from "@/context/AuthContext";
import { Button } from "@/components/ui/button";

export default function NavBar() {
  const { userRole, logout } = useAuth();

  const handleLogout = () => {
    logout();
    window.location.href = "/";
  };

  return (
    <nav className="fixed top-0 left-0 right-0 z-50 bg-black/80 backdrop-blur-sm border-b border-zinc-800">
      <div className="max-w-7xl mx-auto px-4">
        <div className="flex items-center justify-between h-16">
          <Link href="/" className="text-xl font-bold bg-gradient-to-r from-blue-500 to-indigo-600 bg-clip-text text-transparent">
            HPU Sports
          </Link>

          <div className="hidden md:flex items-center space-x-8">
            {!userRole ? (
              // Public navigation
              <>
                <Link href="/#features" className="text-gray-300 hover:text-white transition-colors">
                  Features
                </Link>
                <Link href="/#facilities" className="text-gray-300 hover:text-white transition-colors">
                  Facilities
                </Link>
                <Link href="/#booking" className="text-gray-300 hover:text-white transition-colors">
                  Booking
                </Link>
                <Link href="/auth/login">
                  <Button className="bg-gradient-to-r from-blue-500 to-indigo-600 text-white hover:from-blue-600 hover:to-indigo-700 transition-all duration-300">
                    Login
                  </Button>
                </Link>
              </>
            ) : (
              // Authenticated navigation
              <>
                {userRole === "admin" && (
                  <>
                    <Link href="/admin/dashboard" className="text-gray-300 hover:text-white transition-colors">
                      Dashboard
                    </Link>
                    <Link href="/admin/users" className="text-gray-300 hover:text-white transition-colors">
                      Users
                    </Link>
                    <Link href="/admin/facilities" className="text-gray-300 hover:text-white transition-colors">
                      Facilities
                    </Link>
                  </>
                )}

                {userRole === "employee" && (
                  <>
                    <Link href="/employee/dashboard" className="text-gray-300 hover:text-white transition-colors">
                      Dashboard
                    </Link>
                    <Link href="/employee/bookings" className="text-gray-300 hover:text-white transition-colors">
                      Bookings
                    </Link>
                    <Link href="/employee/customers" className="text-gray-300 hover:text-white transition-colors">
                      Customers
                    </Link>
                  </>
                )}

                {userRole === "customer" && (
                  <>
                    <Link href="/dashboard" className="text-gray-300 hover:text-white transition-colors">
                      Dashboard
                    </Link>
                    <Link href="/booking" className="text-gray-300 hover:text-white transition-colors">
                      Book Now
                    </Link>
                    <Link href="/store" className="text-gray-300 hover:text-white transition-colors">
                      Equipment Store
                    </Link>
                  </>
                )}

                <Button 
                  onClick={handleLogout}
                  variant="outline"
                  className="border-red-500 text-red-500 hover:bg-red-500/10"
                >
                  Logout
                </Button>
              </>
            )}
          </div>
        </div>
      </div>
    </nav>
  );
}
